import React, { useState } from 'react';
import { Form, Card, Row, Col, Button } from 'antd';
import Slide from 'react-reveal/Slide';
// import styles from './Homepage.module.scss';

const HomepageWithForm = ({form}) => {

    const [hide, setHide] = useState({
        login: true,
        signup: false 
    });

    const handleClick= () => {
        setHide({login: !hide.login, signup: !hide.signup})
    }

    return(
        <div>
            <Row>
                <Col span={24}>
                    <Slide opposite cascade left when={hide.login}>
                        <Card style={{position:'absolute', width: '100%'}}>
                            <p>login</p>
                            <p>Card content</p>
                            <p>Card content</p>
                            
                        </Card>
                    </Slide>
                </Col>
                <Col span={24}>
                    <Slide opposite cascade left when={hide.signup}>
                        <Card>
                            <p>Signup</p>
                            <p>Card content</p>
                            <p>Card content</p>
                        </Card>
                    </Slide>
                </Col>
                <Button onClick={handleClick}>
                    { hide.login ? 'SIGNUP' : 'LOGIN' }
                </Button>
            </Row>      
        </div>
    );
}

export const Homepage = Form.create({name:'login'})(HomepageWithForm);