import * as actions from '../actions/LoadingBar';

const initialState = {loading:false};

export const loading = (state = initialState, action) => {
    switch (action.type) {
        case actions.SET_LOADING_PENDING:
            return {
                ...state,
                loading:true
            };
        case actions.SET_LOADING_FULFILLED:
            return {
                ...state,
                loading: false
            };
        default:
        return state;
    }
};