import * as actions from '../actions/authTypes';

const isEmpty = require("is-empty");

const initialState = {
    isAuthenticated: false,
    user: {},
    loading: false
};

export const authData = (state = initialState, action) => {
    switch (action.type) {
        case actions.SETTING_USER_FULFILLED:
            return {
                ...state,
                isAuthenticated: !isEmpty(action.payload),
                user: action.payload
            };
        case actions.SETTING_USER_PENDING:
            return {
                ...state,
                loading: true
            };
        default:
        return state;
    }
};