import React, {useState, useCallback} from 'react';
import { NavLink } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import MaterialButton from '@material-ui/core/Button';
import { Form, Icon, Input, Drawer, Button } from 'antd';
import { useDispatch, useSelector } from 'react-redux';
import { checkLoading } from '../../store/actions/LoadingBar';


// import styles from './Navbar.module.scss';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
}));

const LandingNavbarWithForm = ({form})=> {

  const dispatch = useDispatch()
  const { getFieldDecorator } = form;
  const classes = useStyles();

  const [state, setState] = useState({
    visible: false
  });

  const loadingStart = useCallback(
    () => dispatch(checkLoading()),
    [dispatch]
  )

  const loadingStatus = useSelector(state => state.loading);
  console.log(loadingStatus);
  // const isLoading = dispatch(()=>checkLoading());

  const showDrawer = () => {
    setState({
      ...state,
      visible: true,
    });
  };

  const onClose = () => {
    setState({
      ...state,
      visible: false,
    });
  };

  const handleSubmit = e => {
    e.preventDefault();
    form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };
  
  return (
    <div className={classes.root}>
      <AppBar position="fixed">
        <Toolbar>
          <Typography variant="h6" style={{color:'white'}} className={classes.title}>
            ChatBuddy
          </Typography>
          <MaterialButton onClick={showDrawer} color="inherit">Login</MaterialButton>
        </Toolbar>
      </AppBar>
      <Drawer style={{zIndex: '1200'}} title="LOGIN" width={'35%'} onClose={onClose} visible={state.visible} closable>
      <Form onSubmit={handleSubmit} className="login-form">
        <Form.Item>
          {getFieldDecorator('username', {
            rules: [
              { required: true, message: 'Please input your email!' },
              {type: 'email', message: 'The input is not valid E-mail!'}
            ],
          })(
            <Input
              prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
              placeholder="Email"
            />,
          )}
        </Form.Item>
        <Form.Item>
          {getFieldDecorator('password', {
            rules: [{ required: true, message: 'Please input your Password!' }],
          })(
            <Input
              prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
              type="password"
              placeholder="Password"
            />,
          )}
        </Form.Item>
        <Button type="primary" block htmlType="submit" className="login-form-button">
            Log in
        </Button>
        <div style={{padding:'2px'}}>
          Or <NavLink onClick={loadingStart} to='/register'>register now!</NavLink> 
        </div>
        
      </Form>
        {/* <div className={styles.drawerStyle}>
          <Button className={styles.footerMargin} onClick={onClose}>
            Cancel
          </Button>
          <Button onClick={onClose} type="primary">
            Submit
          </Button>
        </div> */}
      </Drawer>
    </div>
  );
}

export const LandingNavbar = Form.create({name: 'login'})(LandingNavbarWithForm);

